import React, { useEffect, useState } from "react";
import "../App.css";
import RecomCard from "../component/RecomCard";
import HeroCarousel from "../component/HeroCarousel";

const Home = () => {
  const [heroItems, setHeroItems] = useState([]);  // HeroCarousel: 
  const [recs, setRecs] = useState([]);            // RecomCard: 

  useEffect(() => {
    fetch("/db.json")
      .then((r) => r.json())
      .then((data) => {
        const items = Array.isArray(data.items) ? data.items : [];

        // HeroCarousel   
        setHeroItems(items);

        // RecomCard  recommended: true 
        const onlyRecommended = items.filter((m) => m.recommended );
        //console.log(": ", onlyRecommended)

        const firstFour = onlyRecommended.slice(0, 4);
        //console.log("4: ", firstFour)

        setRecs(firstFour);
      })
      .catch((err) => console.error("db.json  :", err));
  }, []);

  return (
    <div className="content">
      {/* 1) HeroCarousel:   */}
      <HeroCarousel items={heroItems} />

      {/* 2)   */}
      <div className="intro-text">
        <h2 className="home-title">
          <span>"  ,</span><br />
          <span>   "</span>
        </h2>
        <div className="text-area">
          <p>
            VeloGear Finder      
              .
            <br />
              ,   ,     
              .
            <br />
                   .
          </p>
        </div>
      </div>

      {/* 3) RecomCard:   */}
      <div className="recommendArea">
        <h2 className="home-title"> PICK</h2>
        <div className="recom-card">
          {recs.map((item) => (
            <RecomCard key={item.id} item={item} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Home;
