// ./pages/DetailPage.jsx
import { useEffect, useMemo, useState } from "react";
import { useLocation, useParams } from "react-router-dom";
import DetailPlus from "./DetailPlus";

export default function DetailPage() {
  const { id } = useParams();
  const { state } = useLocation();
  const [items, setItems] = useState([]);
  const passed = state?.item;

  // state    db.json 
  useEffect(() => {
    if (passed) return;
    fetch("./db.json")
      .then(r => r.json())
      .then(d => setItems(d.items || []))
      .catch(console.error);
  }, [passed]);

  const item = useMemo(() => {
    if (passed) return passed;
    const found = items.find(it => String(it.id) === String(id));
    return found || null;
  }, [passed, items, id]);

  const onAddCart = () => alert(" .");
  const openImage = () => alert(" () ");

  return (
    <section className="detail-page wrap">


      {/* :  DetailPlus   */}
      <DetailPlus onAddCart={onAddCart} openImage={openImage} />
    </section>
  );
}
